<template>
<MyDialog title="IP адреса услуги" :loading="!data">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item v-bind="attrs" v-on="on">
      <v-list-item-icon>
        <v-icon>mdi-network-strength-4</v-icon>
      </v-list-item-icon>
      <v-list-item-title>IP Адреса</v-list-item-title>
    </v-list-item>
  </template>
    <v-container v-if="data">
      <v-list dense style="width: 100%; overflow-x: hidden !important" >
        <Ip v-for="item in data" :key="item.id" :service="service" :item="item"/>
        <NewIp :service="service"/>
      </v-list>
    </v-container>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';
import Ip from '@/render/components/Service/Ip.vue';
import NewIp from '@/render/components/Service/NewIp.vue';

export default {
  components: { Ip, NewIp, MyDialog },
  props: ['service', 'type'],
  data: () => ({
    data: null
  }),

  async mounted() {
    this.data = await this.api.exec('service.ip', { elid: this.service.elid });
  }
};
</script>
