<template>
<v-tooltip bottom v-if="ipData">
  <template v-slot:activator="{ on, attrs }">
    <span v-longclick="update" @dblclick.prevent.stop="update" @click.prevent.stop="() => {}" v-bind="attrs" v-on="on" :style="`color: ${color}`">{{value}}</span>
  </template>
  <span>{{ipData.services.join(', ')}}</span>
</v-tooltip>
</template>

<script>
export default {
  props: ['value'],

  data: () => ({
    ipData: null
  }),

  mounted() {
    this.ipData = this.api.ipService.get(this.value);
    // console.log('id', this.ipData)
  },

  methods: {
    update() {
      this.api.ipService.update(this.ipData);
    }
  },

  computed: {
    color() {
      if (this.ipData.loading) {
        return 'gray';
      }

      return this.ipData.alive ? '#a3e6a3' : 'red';
    }
  }
};
</script>
