<template>
<MyDialog width="500px">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item
          v-bind="attrs" v-on="on"
          three-line
        >
          <v-list-item-content>
            <v-list-item-title><IpParser :value="item.name"/></v-list-item-title>
            <v-list-item-subtitle>{{item.domain}}]</v-list-item-subtitle>
            <v-list-item-subtitle v-if="item.is_main === 'on'">Главный</v-list-item-subtitle>
            <v-list-item-subtitle v-if="item.ip_status !== '2'">Обрабатывается</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
  </template>
  <template v-slot:title><IpParser :value="item.name"/></template>
  <v-container v-if="data">
      <v-text-field v-model="data.domain" label="Домен"/>
      <v-text-field v-model="mask" disabled label="Маска подсети"/>
      <v-text-field v-model="gateway" disabled label="Шлюз"/>
    </v-container>
    <v-card-actions>
      <v-btn color="primary">Сохранить</v-btn>
      <RemoveIp :service="service" :item="item"/>
    </v-card-actions>
</MyDialog>
</template>

<script>
import RemoveIp from '@/render/components/Service/RemoveIp.vue';
import IpParser from '@/render/components/Ticket/Parsers/IpParser';
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { RemoveIp, IpParser, MyDialog },
  props: ['service', 'item'],
  data: () => ({
    data: null,
    domain: '',
    mask: '',
    gateway: ''
  }),

  async mounted() {
    this.data = await this.api.exec('service.ip.edit', { plid: this.service.elid, elid: this.item.id });
    this.domain = this.data.domain;
    this.mask = this.data.mask;
    this.gateway = this.data.gateway;
  }
};
</script>
