<template>
<v-list nav dense>
      <v-list-item @click="() => platform.openUrl(`https://my.msk.host/billmgr?startform=${type}&id=${service.id}&filter=on`)">
        <v-list-item-icon>
          <v-icon>mdi-link</v-icon>
        </v-list-item-icon>
        <v-list-item-title>Открыть в биллинге</v-list-item-title>
      </v-list-item>
      <v-list-item @click="openWindow">
        <v-list-item-icon>
          <v-icon>mdi-door</v-icon>
        </v-list-item-icon>
        <v-list-item-title>Перейти в панель</v-list-item-title>
      </v-list-item>
      <History :service="service" :type="type"/>
      <v-list-item v-if="type && service.status == 3" @click="() => api.exec(`${type}.resume`, { elid: service.elid }).then(() => $emit('update'))">
        <v-list-item-icon><v-icon>mdi-lightbulb-on</v-icon></v-list-item-icon>
        <v-list-item-title>Включить</v-list-item-title>
      </v-list-item>
      <v-list-item v-if="type && service.status == 2" @click="() => api.exec(`${type}.suspend`, { elid: service.elid }).then(() => $emit('update'))">
        <v-list-item-icon><v-icon>mdi-lightbulb-off</v-icon></v-list-item-icon>
        <v-list-item-title>Выключить</v-list-item-title>
      </v-list-item>
      <Ips :service="service" :type="type"/>
    </v-list>
</template>

<script>
import History from '@/render/components/Service/History.vue';
import Ips from '@/render/components/Service/Ips.vue';

export default {
  components: { History, Ips },
  props: ['service', 'type'],

  methods: {
    async openWindow() {
      const goto = await this.api.exec('gotoserver', { elid: this.service.elid }).catch(() => false);
      if (!goto) {
        alert('У этой услуги нет панели управления или она не работает.');
      }

      this.platform.openPanelWindow(goto.ok, this.service.elid);
    }
  }
};
</script>
