<template>
<MyDialog title="Новый IP" :loading="!data">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item
          v-bind="attrs" v-on="on"
        >
          <v-list-item-content>
            <v-list-item-title>Создать</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
  </template>
    <v-container v-if="data">
      <!--<v-checkbox v-model="checkbox" label="Не передавать в панель"/>-->
      <!--<v-checkbox v-model="checkbox" label="Без финансов"/>-->
      <v-text-field v-model="data.domain" label="Домен"/>
    </v-container>
    <v-progress-circular v-else size="14" width="2" class="mr-2" indeterminate color="primary"/>
    <v-card-actions>
      <v-btn color="primary" @click="createIt" :loading="loading">Создать</v-btn>
    </v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['service'],
  data: () => ({
    data: null,
    loading: false
  }),

  async mounted() {
    this.data = await this.api.exec('service.ip.edit', { plid: this.service.elid });
  },

  methods: {
    async createIt() {
      this.loading = true;
      const response = await this.api.exec('service.ip.edit', {
        plid: this.service.elid,
        noexpense: 'on',
        type: this.data.type,
        maxcount: 1,
        manual: 'off',
        domain: this.data.domain,
        count: 1,
        postpaid: 'off',
        needreboot: 'on',
        clicked_button: 'ok',
        sok: 'ok'
      });

      if (response.error) {
        alert(response.error.msg);
        this.loading = false;
        return;
      }

      alert('Успех блин');
      this.loading = false;
    }
  }
};
</script>
