<template>
<MyDialog :title="`Удаление ${item.name}`" :loading="!data">
  <template v-slot:activator="{ on, attrs }">
    <v-btn color="error" v-bind="attrs" v-on="on">Удалить</v-btn>
  </template>
  <v-container v-if="data">
      <!--<v-checkbox v-model="checkbox" label="Не передавать в панель"/>-->
      <!--<v-checkbox v-model="checkbox" label="Без финансов"/>-->
      <!--<v-text-field v-model="cost" label="Цена"/>-->
    </v-container>
    <v-card-actions>
      <v-btn color="error" @click="deleteIt" :loading="loading">Удалить</v-btn>
    </v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['service', 'item'],
  data: () => ({
    data: null,
    loading: false
  }),

  async mounted () {
    this.data = await this.api.exec('service.ip.del.admin', { plid: this.service.elid, elid: this.item.id })
    this.cost = this.data.cost
  },

  methods: {
    async deleteIt () {
      this.loading = true
      const response = await this.api.exec('service.ip.del.admin', {
        plid: this.service.elid,
        elid: this.item.id,
        noexpense: 'on',
        manual: 'off',
        ipcount: 1,
        cost: 0.00,
        clicked_button: 'ok',
        sok: 'ok'
      })

      if (response.error) {
        alert(response.error.msg)
        this.loading = false
        return
      }

      alert('Успех блин')
      this.loading = false
    }
  }
}
</script>
