<template>
<MyDialog width="100%" :loading="!data" title="История услуги">
  <template v-slot:activator="{ on, attrs }">
    <v-list-item v-bind="attrs" v-on="on">
      <v-list-item-icon><v-icon>mdi-book</v-icon></v-list-item-icon>
      <v-list-item-title>История</v-list-item-title>
    </v-list-item>
  </template>
  <v-container v-if="data">
    <v-simple-table dense>
      <thead><tr><th>Дата</th><th>Описание</th><th>Кто</th><th>IP</th><th>Функция</th></tr></thead>
      <tbody>
        <tr v-for="(item, key) in data" :key="key">
          <td>{{ item.changedate }}</td>
          <td>{{ item.desc }}</td>
          <td>{{ item.user }}</td>
          <td>{{ item.ip }}</td>
          <td>{{ item.action }}</td>
        </tr>
      </tbody>
    </v-simple-table>
  </v-container>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  props: ['service', 'type'],
  data: () => ({
    data: null
  }),

  asyncComputed: {
    data() {
      return this.api.exec('service.history', { elid: this.service.elid })
        .then(v => v.sort((a, b) => Date.parse(b.changedate) - Date.parse(a.changedate)));
    }
  }
};
</script>
